import axios from 'axios'
import { Message } from 'element-ui'
import router from '@/router/index'
const service = axios.create({
  baseURL: process.ENV === 'production' ? 'http://dev.z2a.com.cn:9088/v1' : 'http://dev.z2a.com.cn:9088/v1',
  withCredentials: false,
  timeout: 30000
})
// request interceptor
service.interceptors.request.use(
  config => {

    if (config.method === 'post' && !config.url.includes('192.168.20.117:9001')) {
      const dataType = Object.prototype.toString.call(config.data) === '[object FormData]'
      if(dataType) {
        return config
      } else {
        const querystring = require('querystring')
        config.data = querystring.encode(config.data)
        
      }
    } else {
      return config;
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  resp => {
    const ret = resp.data
    if (ret.status === 200) {
      return ret
    } else {
      return ret
    }
  },
  error => {    
    const { response } = error;
    
      if (response) {
        Message({
          message: '请求出错，请重新进行尝试！',
          type: 'error',
          duration: 3 * 1000
        })
        return Promise.reject(error)
      } else {
        debugger
        Message({
          message: '请网络异常！',
          type: 'error',
          duration: 3 * 1000
        });
        router.push({ path: '/netWorkError' });
      }
  }
)

export default service
