<template>
  <div class="login-container">
    <el-form
      ref="loginForm"
      :model="loginForm"
      :rules="loginRules"
      class="login-form"
      autocomplete="on"
      label-position="left"
    >
      <div class="title-container">
        <h3 class="title">东天红智能过户平台</h3>
      </div>

      <el-form-item prop="username">
        <span class="svg-container">
          <svg-icon icon-class="user" />
        </span>
        <el-input
          ref="username"
          v-model="loginForm.username"
          placeholder="Username"
          name="username"
          type="text"
          tabindex="1"
          autocomplete="on"
        />
      </el-form-item>

      <el-tooltip v-model="capsTooltip" content="Caps lock is On" placement="right" manual>
        <el-form-item prop="password">
          <span class="svg-container">
            <svg-icon icon-class="password" />
          </span>
          <el-input
            :key="passwordType"
            ref="password"
            v-model="loginForm.password"
            :type="passwordType"
            placeholder="Password"
            name="password"
            tabindex="2"
            autocomplete="on"
            @blur="capsTooltip = false"
            @keyup.enter.native="handleLogin"
          />
          <span class="show-pwd" @click="showPwd">
            <svg-icon :icon-class="passwordType === 'password' ? 'eye' : 'eye-open'" />
          </span>
        </el-form-item>
      </el-tooltip>
      <el-button
        :loading="loading"
        type="primary"
        style="width:100%;margin-bottom:30px;background-color: #169bd5;
    border-color: #169bd5;"
        @click.native.prevent="handleLogin"
      >Login</el-button>

      <div style="position:relative">
        <!--<div class='tips'>
          <span>角色名 : admin</span>
          <span>角色名 : other</span>
        </div>
        <div class='tips'>
          <span style='margin-right:18px;'>角色名 : editor</span>
          <span>密&nbsp;&nbsp;码 : any</span>
        </div>-->

        <!--<el-button class='thirdparty-button' type='primary' @click='showDialog=true'>
          Or connect with
        </el-button>-->
      </div>
    </el-form>

    <!--<el-dialog title='Or connect with' :visible.sync='showDialog'>
      Can not be simulated on local, so please combine you own business simulation! ! !
      <br>
      <br>
      <br>
      <social-sign />
    </el-dialog>-->
  </div>
</template>

<script>
 import { validUsername } from "@/utils/validate";
// import { setCookie, getStorage, setStorage  } from "@/utils/auth";
// import { mapGetters } from 'vuex'
import { Message } from "element-ui";
// import { getRolesList, getMenusList } from '@/api/user';
export default {
  name: "Login",
  data() {
    const validateUsername = (rule, value, callback) => {
      if (!validUsername(value)) {
        callback(new Error("Please enter the correct user name"));
      } else {
        callback();
      }
    };
    const validatePassword = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error("The password can not be less than 6 digits"));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        username: "duxn",
        password: "123123"
      },
      loginRules: {
        username: [
          { required: true, trigger: "blur", validator: validateUsername }
        ],
        password: [
          { required: true, trigger: "blur", validator: validatePassword }
        ]
      },
      passwordType: "password",
      capsTooltip: false,
      loading: false,
      showDialog: false,
      redirect: undefined,
      otherQuery: {}
    };
  },
  mounted() {
    /**
     * 切换登录页和首页logo显示状态
     *
     */

    
    
    this.$store.dispatch("web/doLoginLayoutStyle");
    this.$store.dispatch("web/toggleLogo");
    this.$nextTick(() => {
      this.doFullScreen()
    })
  },
  destroyed() {
    this.$store.dispatch("web/toggleLogo");
    this.$store.dispatch("web/doLoginLayoutStyle");
  },
  methods: {
    doFullScreen() {
      const element = document.documentElement;
        if(element.requestFullscreen) {
            
        element.requestFullscreen();
        } else if (element.mozRequestFullScreen){
        element.mozRequestFullScreen();
        } else if(element.webkitRequestFullscreen) {
        element.webkitRequestFullscreen();
        } else if (element.msRequestFullscreen) {
        element.msRequestFullscreen();
        }
    },
    showPwd() {
      if (this.passwordType === "password") {
        this.passwordType = "";
      } else {
        this.passwordType = "password";
      }
      this.$nextTick(() => {
        this.$refs.password.focus();
      });
    },
    handleLogin() {
      const { username, password } = this.loginForm;
      this.$store
        .dispatch("user/doLogin", { userName: username, passWord: password })
        .then(
          async resp => {
            const { status } = resp;
            if (status === 200) {
              Message({
                message: resp.message || "Error",
                type: "success",
                duration: 3 * 1000
              });
              this.$router.push({ path: "/doInvoice" });  
                
            }
          },
          err => {
            Message({
              message: err || "Error",
              type: "error",
              duration: 3 * 1000
            });
          }
        )
        .catch(err => {
          Message({
            message: err || "Error",
            type: "error",
            duration: 3 * 1000
          });
          this.$router.push({ path: "/login" });
        });
    }
  }
};
</script>
<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  overflow: hidden;

  .login-form {
    position: relative;
    width: 372px;
    max-width: 100%;
    padding: 100px 25px 0;
    margin: 0 auto;
    overflow: hidden;
    float: right;
    margin-right: 300px;
    border: 1px solid #dedede;
    margin-top: 64px;
    background-color: #fff;
    border-radius: 8px;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 30px;
    display: inline-block;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: #169bd5;
      margin: 0px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }

  .thirdparty-button {
    position: absolute;
    right: 0;
    bottom: 6px;
  }

  @media only screen and (max-width: 470px) {
    .thirdparty-button {
      display: none;
    }
  }
}
</style>
