/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
    return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
    const valid_map = ['luruyuan', 'admin', 'info', 'yanche', 'kaipiao', 'guohu']
    return valid_map.indexOf(str.trim()) >= 0
}

/**
 * @param {string} url
 * @returns {Boolean}
 */
export function validURL(url) {
    const reg = /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/
    return reg.test(url)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validLowerCase(str) {
    const reg = /^[a-z]+$/
    return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUpperCase(str) {
    const reg = /^[A-Z]+$/
    return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validAlphabets(str) {
    const reg = /^[A-Za-z]+$/
    return reg.test(str)
}

/**
 * @param {string} email
 * @returns {Boolean}
 */
export function validEmail(email) {
    const reg = /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return reg.test(email)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function isString(str) {
    if (typeof str === 'string' || str instanceof String) {
        return true
    }
    return false
}

/**
 * @param {Array} arg
 * @returns {Boolean}
 */
export function isArray(arg) {
    if (typeof Array.isArray === 'undefined') {
        return Object.prototype.toString.call(arg) === '[object Array]'
    }
    return Array.isArray(arg)
}

/**
 * 
 * @param { String } str
 * @return { Boolean } 
 */
export function isNotEmpty(str) {
    return str !== ''
}

/**
 * 
 * @param { String } val
 * @return { Boolean } 
 */
export function isNotOneOf(val) {
    const roles = ['录入员', '查询员', '验车员', '审核员', '过户员', '开票员', '管理员']
    return roles.includes(val)
}


/**
 * 
 * @param { String } val
 * @return { Boolean } 
 */
export function isNoLength(val) {
    return val.length > 4
}
/**
 * 
 * @param { String } phone
 * @return { Boolean } 
 */
export function isPhone(phone) {
    var reg = /^1[3|4|5|7|8][0-9]{9}$/;
    if (! reg.test(phone)) {
        return false
    }
    return true
}

/**
 * 
 * @param { String } idCard
 * @return { Boolean } 
 */

export function isIdCard(idCard) {
    const aCity = {
        11: "北京",
        12: "天津",
        13: "河北",
        14: "山西",
        15: "内蒙古",
        21: "辽宁",
        22: "吉林",
        23: "黑龙江",
        31: "上海",
        32: "江苏",
        33: "浙江",
        34: "安徽",
        35: "福建",
        36: "江西",
        37: "山东",
        41: "河南",
        42: "湖北",
        43: "湖南",
        44: "广东",
        45: "广西",
        46: "海南",
        50: "重庆",
        51: "四川",
        52: "贵州",
        53: "云南",
        54: "西藏",
        61: "陕西",
        62: "甘肃",
        63: "青海",
        64: "宁夏",
        65: "新疆",
        71: "台湾",
        81: "香港",
        82: "澳门",
        91: "国外"
    }
    var iSum = 0
    if (!/^\d{17}(\d|x)$/i.test(idCard)) {
        return false
    }
    idCard = idCard.replace(/x$/i, "a");
    if (aCity[parseInt(idCard.substr(0, 2))] == null) {
        return false
    }
    var sBirthday = idCard.substr(6, 4) + "-" + Number(idCard.substr(10, 2)) + "-" + Number(idCard.substr(12, 2))
    var d = new Date(sBirthday.replace(/-/g, "/"))
    if (sBirthday != (d.getFullYear() + "-" + (
        d.getMonth() + 1
    ) + "-" + d.getDate())) {
        return false
    }
    for (var i = 17; i >= 0; i--) {
        iSum += (Math.pow(2, i) % 11) * parseInt(idCard.charAt(17 - i), 11)
    }
    if (iSum % 11 != 1) {
        return false
    }

    return true;

}


/**
 * 
 * @param { Number } num
 * @return { Boolean } 
 */

export function isNumber(num){
  const isNum = /^\d+(\.\d+)?$/
  if(!isNum.test(num)){
      return false
  }else{
      return true
  }
}


/**
 * @param { String } emailStr
 * @return { Boolean } 
 */
export function isEmail(email){  
  const reg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;   
  if(!reg.test(email)){  
      return false;  
  }  
  return true;  
}