import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '../layout/index.vue'
import Login from '../views/login/index'
Vue.use(VueRouter)
const routerPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return routerPush.call(this, location).catch(error=> error)
}
const routes = [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: '登录',
      hasBread: true
    },
    component: Login
  },
  {
    path: '/guide',
    name: 'guide',
    meta: {
      title: '使用指南',
      hasBread: false,
      guideInfo: {
        guideInfoTitle: '使用指南'
      }
    },
    component: () => import('../views/Guide.vue')
  },
  {
    path: '/netWorkError',
    name: 'netWorkError',
    meta: {
      title: '网络错误',
      hasBread: false,
      netWorkErrorInfo: '网络错误'
    },
    component: () => import('../views/NetWorkError.vue')
  },
  {
    path: '/exception',
    name: 'exception',
    meta: {
      title: '硬件故障',
      hasBread: false,
      netWorkErrorInfo: '硬件故障'
    },
    component: () => import('../views/Error.vue')
  },
  {
    path: '/bioassay',
    name: 'bioassay',
    meta: {
      title: '活体检测',
      hasBread: false,
      netWorkErrorInfo: '活体检测'
    },
    component: () => import('../views/Bioassay.vue')
  },
  {
    path: '/doInvoice',
    name: 'doInvoice',
    meta: {
      title: '打印发票',
      hasBread: false,
      cardInfoData: {
        cardInfoTitle: '打印发票'
      }
    },
    component: () => import('../views/DoInvoice.vue'),
  },
  {
    path: '/blank',
    name: 'blank',
    meta: {
      title: '',
      hasBread: false,
      cardInfoData: {
        cardInfoTitle: ''
      }
    },
    component: () => import('../views/Blank.vue'),
  },
  {
    path: '/',
    redirect: '/login'
  },
  {
    path: '/vechicleChecker',
    component: Layout,
    redirect: '/vechicleChecker/index',
    children: [{
      path: 'makeInvoice',
      name: 'makeInvoice',
      meta: {
        makeInvoiceData: {
          makeInvoiceTitle: '二手车销售统一发票'
        },
        title: '开票员',
        hasBread: false,
        role: {
          base: 1,
          admin: 0,
          superAdmin: 0
        }
      },
      component: () => import('../views/MakeInvoice.vue')
    }, {
      path: 'printInvoice',
      name: 'printInvoice',
      meta: {
        makeInvoiceData: {
          makeInvoiceTitle: '开票'
        },
        title: '开票员',
        hasBread: false,
        role: {
          base: 1,
          admin: 0,
          superAdmin: 0
        }
      },
      component: () => import('../views/PrintInvoice.vue')
    }]
  },
  {
    path: '/faceInfo',
    name: 'faceInfo',
    meta: {
      title: '活体检测',
      hasBread: false,
      faceInfoData: {
        faceInfoTitle: '活体检测'
      }
    },
    component: () => import('../views/FaceInfo.vue'),
  },
  {
    path: '/cardInfo',
    name: 'cardInfo',
    meta: {
      title: '身份证信息验证',
      hasBread: false,
      cardInfoData: {
        cardInfoTitle: '身份证信息验证'
      }
    },
    component: () => import('../views/CardInfo.vue'),
  },
  {
    path: '*',
    component: () => import('../views/Error.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
