// import Cookie from 'js-cookie'
import { getCookie } from '@/utils/auth'
import { loginAction } from '@/api/user'
const state = {
  session: getCookie(),
  name: '',
  avatar: '',
  introduction: '',
  roles: [],
  userId: '',
  roleObj: null
}

const mutations = {
  SET_SESSION: (state, session) => {
    state.session = session
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_INTRODUCTION: (state, instruction) => {
    state.instruction = instruction
  },
  SET_ROLES: (state, roles) => {
    state.roles = roles
  },
  SET_USERID: (state, userId) => {
    state.userId = userId
  },
  SET_ROLEOBJ: (state, roleObj) => {
    state.roleObj = roleObj
  }
}

const actions = {
  doLogin({ commit }, loginForm) {
    console.log(commit)
    const { userName, passWord } = loginForm
    // const session = 'N2RmMmQ5MzUtYTEyOS00MTQ4LWEyOGUtZGEwYTlmMDU3OGRl'
    return new Promise((resolve, reject) => {
      loginAction({ userName: userName.trim(), passWord: passWord }).then(resp => {
        
        resolve(resp)
      }).catch(error => {
        
        reject(error)
      })
    })
  },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
