// import Cookies from 'js-cookie'

const state = {
  logo: {
    opened: 0
  },
  loginLayoutStyle: false
}

const mutations = {
  TOGGLE_LOGO: state => {
    state.logo.opened = !state.logo.opened
  },
  DO_LOGIN_LAYOUT_STYLE: state => {
    state.loginLayoutStyle = !state.loginLayoutStyle
  }
}

const actions = {
  toggleLogo({ commit }) {
    commit('TOGGLE_LOGO')
  },
  doLoginLayoutStyle({ commit }) {
    commit('DO_LOGIN_LAYOUT_STYLE')
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
